import { Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { changeName, increase } from "../store/userSlice.js"
import { useState } from "react"
import { addCount, minusItem, deleteState, deleteevery } from "../store.js"
import { First } from "react-bootstrap/esm/PageItem.js"


function Cart(props) {
  let Night = props.Night
  let [i, seti] = useState(0)
  let state = useSelector((state) => { return state })
  let Price = 0
  let FinalPrice = 0


  let dispatch = useDispatch()
  console.log()
  let o = 0





  return (
    < div id="Table-site" style={Night ? { backgroundColor: "#202124", color: 'rgb(224, 224, 215)', borderColor: "white" } : { backgroundColor: 'rgb(224, 224, 215)', color: 'rgb(63, 63, 63)', borderColor: "black" }}>
      <h1 id="Table-title" ></h1>
      <Table >
        <thead style={{ backgroundColor: "black", border: "1px ridge black", borderRadius: "0.5rem" }}>


          <tr style={{ backgroundColor: "black", border: "1px ridge black", borderRadius: "0.5rem", }}>

            <th width="130px">#</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>

        <tbody>


          {

            state.cart.map((a, i) => {
              Price = state.cart[i].price * (state.cart[i].count + 1)
              FinalPrice = FinalPrice + Price
              console.log(state.cart[i])


              return (

                <tr key={i} id="td" style={{ border: "1px ridge black", borderRadius: "0.5rem" }}>
                  <td><img src={`https://jaewon1020.github.io/shoes${state.cart[i].id + 1}.png`} width="100px"></img></td>
                  <td id="td">{state.cart[i].name}</td>
                  <td id="td">{state.cart[i].count + 1}</td>
                  <td style={{ paddingTop: "20px" }}>{state.cart[i].price * (state.cart[i].count + 1)}</td>

                  <td id="td" style={{ fontSize: "20px", paddingTop: "5px" }}>
                    <button style={{ backgroundColor: "rgb(153,153,153)", border: "rgb(153,153,153)", }} onClick={() => {
                      dispatch(addCount(state.cart[i].id))
                    }}>+</button>/
                    <button style={{ fontSize: "30px", backgroundColor: "rgb(153,153,153)", border: "rgb(153,153,153)" }} onClick={() => {
                      if (state.cart[i].count + 1 <= 0) {
                        alert(' .')
                      } else {
                        dispatch(minusItem(state.cart[i].id))
                      }

                    }}>-</button>

                  </td>
                  <td>
                    <button id="td1" onClick={() => {
                      dispatch(deleteState(state.cart[i].id))
                      console.log(state.cart)
                    }}>

                      X</button>
                  </td>

                </tr>

              )
            })
          }


        </tbody>


      </Table >
      <div className="buy">
        <p className="buy1" onClick={() => {
          console.log(state.cart)
          if (state.cart.length === 0) {
            console.log(state.cart)
            alert(" ")
          } else {
            alert(" ")
            dispatch(deleteevery())
          }

          console.log(state)
        }} style={{ float: "right", border: "1px", paddingTop: "15px", fontSize: "19px", fontWeight: "600" }}></p>
        <p className="buy1" style={{ marginLeft: "75%", paddingTop: "15px", fontSize: "19px" }}>{FinalPrice}</p>




      </div>



    </div >
  )
}

export default Cart