import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';


function Login({ Night, , setSlide }) {
  let navigate = useNavigate()
  const [idValue, setId] = useState('');
  const saveUserId = event => {
    setId(event.target.value);
    console.log(event.target.value);
  };
  return (

    <div style={{ paddingTop: "100px", fontFamily: 'Roboto,sansSerif' }}>


      <Form className='' style={Night ? { backgroundColor: "rgb(80, 78, 78)" } : { backgroundColor: "white" }}>
        <div style={{ marginLeft: "2%" }}>
          <h4 style={{ textAlign: "left", width: "95%", float: "left", fontSize: "20px", padding: "20px", paddingRight: "20px", marginLeft: "10px", marginRight: "10px", borderBottom: "3px #b9b9b9 solid" }}>FitPlus</h4>
        </div>

        <div id='LoginTitle' style={Night ? { color: "#4880c0" } : { color: "rgb(81, 32, 145)" }}>Login</div>
        <Form.Group className="mb-3" controlId="formBasicEmail" style={{ marginLeft: "10%", marginRight: "40%", paddingTop: "5%", }}>

          <Form.Control value={idValue} onChange={saveUserId} type="email" placeholder="" style={{ backgroundColor: "#F8F8F8", borderColor: "#F8F8F8", width: "150%", height: "70px" }} />

        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicPassword" style={{ marginLeft: "10%", marginRight: "40%" }}>

          <Form.Control type="password" placeholder="" style={{ backgroundColor: "#F8F8F8", borderColor: "#F8F8F8", width: "150%", height: "70px" }} />
        </Form.Group>

        <Button variant="primary" type="submit" id='loginButton' style={Night ? { backgroundColor: "#4880c0" } : { backgroundColor: "rgb(81, 32, 145)" }}
          onClick={() => {
            if (idValue == '') {
              setSlide(false)
              alert(" ...")

            } else {
              (idValue)
              setSlide(true)
              navigate('/')
            }

          }}
        >
          Login
        </Button>
      </Form>
    </div >

  );
}

export default Login;